.pagination {
  max-width: 100%;
  justify-content: center;
  margin-bottom: calc(var(--padding-and-margin) * 2);

  & .page-item {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  & .page-link {
    text-decoration: none;
    color: $text-color;
    border-radius: 5px;

    &[aria-label] {
      font-size: 160%;
      line-height: 0.8;
    }

    &[aria-label="Previous"] {
      opacity: 0.5;
      font-weight: 600;
    }
    &[aria-label="Next"] {
      color: $primary-color;
      font-weight: 600;
    }
  }
}
