.hotel-preview {
  padding: $padding-and-margin;
  margin-bottom: $padding-and-margin;
  background-color: $bg-color;
  border-radius: 10px;
  font-size: 1rem;

  &__title {
    margin-bottom: 0;
  }

  &__header {
    margin-bottom: $padding-and-margin;
  }

  &__pic {
    margin-bottom: $padding-and-margin;
    height: 15rem;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: $primary-color;
    border-radius: 10px;
  }
  &__info {
    margin-bottom: $padding-and-margin;
  }
  &__item {
    margin-bottom: 5px;
  }
  &__label {
    opacity: 0.7;
  }
  &__val {
    padding-left: 10px;
  }
  &__price {
    & > * {
      @include for-size(tablet-landscape-up) {
        display: flex;
        flex-direction: column;
      }
    }
  }
  &__price-label {
    opacity: 0.7;
  }
  &__price-number {
    @include ff("font-size", $mainTitleh1);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    line-height: normal;
  }
}

@media (max-width: 767px) {
  .hotel-preview {
    position: relative;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-bottom: 5px;
    font-size: 0.8rem;
    line-height: normal;
  }

  .hotel-preview__city {
    display: none;
  }

  .hotel-preview__rating {
    display: none;
  }

  .hotel-preview__header {
    margin-left: auto;
    margin-right: 0;
    margin-bottom: 0;
    width: 80%;
  }

  .hotel-preview__info {
    margin-left: auto;
    margin-right: 0;
    margin-bottom: 0;
    width: 80%;
  }

  .hotel-preview__price {
    margin-left: auto;
    margin-right: 0;
    margin-bottom: 0;
    padding-top: 5px;
    margin-top: 5px;
    text-align: right;
    border-top: 1px solid $border-base;
  }

  .hotel-preview__pic {
    position: absolute;
    top: 15px;
    left: 15px;
    width: 15%;
    height: 4rem;
    max-height: 10rem;
    border-radius: 0;
  }

  .hotel-preview__item {
    margin-bottom: 0;
  }

  .hotel-preview__price-number {
    font-size: 1rem;
  }
}
