.map {
  position: relative;
  background-color: $primary-color;
  height: 100%;
  min-height: 350px;
  margin-bottom: 20px;
  overflow: hidden;

  @include for-size(phone-only) {
    min-height: 550px;
    height: 100%;
    margin-bottom: 0;
    overflow: hidden;
  }
}

#map {
  // overflow: visible !important;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  overflow: hidden;


}
