.step-authorization {
  @include grid-col(
    (
      xs: 12,
      sm: 8
    )
  );

  &--popup {
    @include grid-col(
      (
        xs: 12
      )
    );
  }

  &__list {
    width: 100%;
    @include listReset();
    text-align: center;
  }
  &__title {
    margin-bottom: calc(var(--padding-and-margin) / 2);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff("font-size", $mainTitleh2);
  }
  &__desc {
    color: $light-color;
  }
}
