.step-contacts {
  &__header {
    padding: var(--padding-and-margin) 0;
    margin-bottom: var(--padding-and-margin);
    text-align: center;
  }
  &__body {
    margin-bottom: var(--padding-and-margin);
    border-bottom: 1px solid $border-base;

    & p {
      text-align: center;
    }
  }
  &__footer {
    padding: var(--padding-and-margin) 0;
    margin-bottom: var(--padding-and-margin);
    text-align: center;
  }
  &__icon {
    width: 6.25rem;
    height: 6.25rem;
  }
}
