.step-making {
  @include grid-container();

  @include for-size(phone-only) {
    padding: 0;
  }
  &__header {
    margin-bottom: var(--padding-and-margin);
    text-align: center;
    color: $light-color;
  }
  &__inner {
    margin-bottom: var(--padding-and-margin);
  }
  &__footer {
    margin: auto;
    padding: var(--padding-and-margin) 0;
    text-align: center;
  }
  &__title {
    margin-bottom: var(--padding-and-margin);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    @include ff("font-size", $mainTitle);
    color: $text-color;
  }
  &__item-title {
    margin-bottom: var(--padding-and-margin);
    margin-top: var(--padding-and-margin);
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;

    text-align: center;
  }
  &__item {
    padding: calc(var(--padding-and-margin) / 2);
    margin-bottom: var(--padding-and-margin);
    background-color: $default-color;
    @include grid-container();

    & .page-form {
      padding: 0;
      @include grid-row();
      background-color: $default-color;
    }
  }
  &__row {
    @include grid-row();
  }
  &__card {
    @include grid-col(
      (
        xs: 12,
        sm: 5,
        lg: 3
      )
    );
  }
  &__content {
    @include grid-col(
      (
        xs: 12,
        sm: 7,
        lg: 8
      )
    );
  }
}
