.hotel-page {
  font-size: 1rem;
  color: #1a1a1a;
  &__rating {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 40px;

    & > *:not(:last-child) {
      margin-right: 10px;
    }
  }
  &__gallery {
    margin-bottom: 40px;
    @include grid-row();

    & .swiper-slide {
      background-size: cover;
      background-position: center;
    }
    & .gallery-top {
      @include grid-col(
        (
          xs: 12,
          md: 8,
          lg: 8
        )
      );
      padding: 0;

      &.swiper-container {
        height: 200px;

        @include for-size(tablet-portrait-up) {
          height: 300px;
        }
        @include for-size(tablet-landscape-up) {
          height: 500px;
        }
      }
    }
    & .gallery-thumbs {
      & .swiper-slide {
        width: 100px !important;
        @include for-size(tablet-landscape-up) {
          width: auto !important;
        }
      }
      @include grid-col(
        (
          xs: 12,
          md: 4,
          lg: 4
        )
      );

      &.swiper-container {
        height: 100px;
        margin-top: 10px;

        @include for-size(tablet-landscape-up) {
          height: 500px;
          margin-top: 0;
        }
      }
    }
  }
  &__content {
    margin-bottom: 40px;
    @include grid-row();
  }
  &__description {
    margin-bottom: 40px;

    @include grid-col(
      (
        xs: 12
      )
    );
  }
  &__info {
    column-count: 2;
    column-width: 200px;
    page-break-inside: avoid;
    break-inside: avoid;
    @include grid-col(
      (
        xs: 12,
        md: 6,
        lg: 8
      )
    );

    & h2,
    & h3,
    & h4,
    & h5 {
      margin-bottom: 20px;
      padding-bottom: 5px;
      border-bottom: 1px solid $border-base;
    }
    & p,
    & ul {
      margin-bottom: 20px;
    }
  }
  &__reviews {
    display: flex;
    flex-direction: column;
    @include grid-col(
      (
        xs: 12,
        md: 6,
        lg: 4
      )
    );

    & .link--white {
      align-self: flex-end;
    }
  }
  & .link--white {
    display: inline-block;
    padding: 0.625rem 2.25rem;
    border-radius: 0.3125rem;
  }
  &__reviews-title {
    margin-bottom: 20px;
  }
  &__map {
    position: relative;

    & .link {
      position: absolute;
      right: 30px;
      top: 60px;
      z-index: 2;
    }
  }
}
