.hotel-review {
  padding: 15px;
  margin-bottom: 1.25rem;
  border: 1px solid $border-base;
  border-radius: 3px;
  &__body-header > *:not(:last-child) {
    margin-right: 10px;
  }
  &__date {
    text-align: right;
  }
  &__num {
    padding: 5px 15px;
    color: #ffffff;
    background-color: #298500;
  }
  &__mark {
    color: $primary-color;
  }
  &__footer {
    text-align: right;
  }
}
