.step-registration {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  &__title {
    margin-bottom: $padding-and-margin;
    text-align: center;
  }
  &__inner {
    margin-bottom: $padding-and-margin;
    background-color: $default-color;
    // max-height: 12.5rem;
    // overflow-y: auto;

    @include for-size(tablet-portable-up) {
      padding: $padding-and-margin;
    }

    & .conetnt {
      height: 100%;
    }
  }
}
