.filters {
  display: flex;
  flex-wrap: wrap;
  max-width: 100%;
  margin-bottom: $padding-and-margin;
  font-family: $mainFont-bold;
  font-weight: $mainFontWeight-bold;
  color: $primary-color;
  &__item {
    display: flex;
    align-items: center;
    flex-grow: 1;
    &:not(:last-child) {
      margin-right: 0.9375rem;
    }

    @include for-size(phone-only) {
      margin-bottom: 1rem;

      &:not(:last-child) {
        margin-right: 0;
      }

      & .d-flex {
        width: 100%;
        font-size: small;
      }
    }
  }
  &__label {
    white-space: nowrap;
    margin-right: 10px;
  }
  & .custom-select {
    min-width: 4rem;
  }
  & .custom-select-trigger {
    padding-right: 20px;
  }
  & .custom-options {
    left: -110px;
    min-width: 11.25rem;
  }
  &__icon {
    padding-left: 10px;
    width: 0.9375rem;
    height: 0.9375rem;
    box-sizing: content-box;
  }
  & .custom-select-trigger:after {
    top: 80%;
    border: 0;
    transform: rotate(180deg);

    background-size: contain;
    background-position: bottom;
    background-repeat: no-repeat;
    background-image: url("../img/icons/icon-arrow_sort_up.svg");
  }
  & .custom-select.opened .custom-select-trigger:after {
    transform: rotate(0deg) translateY(-150%);
  }
}
