.chat {
  &__item {
    margin-bottom: $padding-and-margin;

    &:nth-child(2n + 2) {
      text-align: right;

      & .chat__item-text {
        & .triangle {
          right: 30px;
          left: auto;
        }
      }
    }
    @include for-size(tablet-portrait-up) {
      & .chat__item-text {
        margin-right: 10%;
      }

      &:nth-child(2n + 2) {
        text-align: right;

        & .chat__item-text {
          margin-left: 10%;
          margin-right: 0;
          & .triangle {
            right: 30px;
            left: auto;
          }
        }
      }
    }

    &-header {
      margin-bottom: $padding-and-margin;
    }

    &-name {
      color: $primary-color;
    }

    &-text {
      position: relative;
      padding: 1rem;

      font-size: 14px;
      line-height: normal;

      background-color: #fff;
      border: 1px solid $border-base;
      border-radius: 5px;

      @include for-size(tablet-portrait-up) {
        padding: $padding-and-margin;
        font-size: 1rem;
      }

      & .triangle {
        width: 0;
        height: 0;
        display: block;
        position: absolute;
        top: -13px;
        left: 30px;
        border-bottom: 15px solid #fff;
        border-left: 15px solid transparent;
        transform: rotate(90deg);
        &::before {
          content: "";
          width: 14px;
          display: block;
          position: absolute;
          top: 4px;
          left: -15px;
          border-top: 1px solid #aaa;

          transform: rotate(-34deg);
        }
        &::after {
          content: "";
          width: 13px;
          display: block;
          position: absolute;
          top: 12px;
          left: -15px;
          border-top: 1px solid #aaa;
          transform: rotate(30deg);
        }
      }
    }
  }
}
