.steps {
  display: none;
  margin-right: auto;

  overflow: hidden;

  @include for-size(tablet-portrait-up) {
    display: block;
    padding: 2.25rem 1rem;
  }

  &__list {
    @include listReset();
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item {
    position: relative;
    padding: $padding-and-margin 0;

    display: flex;
    align-items: center;

    @include for-size(tablet-portrait-up) {
      display: block;

      &:not(:last-child) {
        margin-right: $padding-and-margin;
      }

      &:not(:last-child)::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 130%;
        transform: translateY(-50%);
        width: 9.5rem;
        height: 2px;
        border: 1px dashed #e6e6e6;

        @include for-size(phone-only) {
          display: none;
        }
        @include for-size(tablet-portrait-up) {
          left: 90%;
          width: 4rem;
        }
        @include for-size(tablet-landscape-up) {
          left: 90%;
          width: 7.5rem;
        }
        @include for-size(desktop-up) {
          left: 90%;
          width: 7rem;
        }
      }
    }

    &--hidden-sm {
      display: block;
      @include for-size(tablet-portrait-down) {
        display: none;
      }
    }

    &.active {
      @include for-size(tablet-portrait-down) {
        margin: 0 auto;
        padding: 0;
      }
      &:not(:last-child)::after {
        border-color: $primary-color;
      }
      & .steps__number {
        background-color: $primary-color;
      }
      & .steps__title {
        color: $text-color;
      }
    }
  }

  &__number {
    margin-right: $padding-and-margin;
    padding: 0.3rem 0.7rem;
    font-weight: 600;
    color: #ffffff;
    background-color: #e6e6e6;
    border-radius: 50%;

    @include ff(
      "font-size",
      (
        375px: 16px,
        750px: 25px
      )
    );

    @include for-size(tablet-portrait-up) {
      padding: 0.625rem 1.25rem;
    }
  }
  &__title {
    font-family: $mainFont-bold;
    font-weight: $mainFontWeight-bold;
    font-size: 12px;
    text-align: center;
    color: #e6e6e6;

    @include for-size(tablet-portrait-up) {
      position: absolute;
      left: 50%;
      bottom: -30px;
      width: 12.5rem;
      transform: translateX(-50%);
    }

    @include for-size(tablet-landscape-up) {
      font-size: 1rem;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 100%;

    max-height: calc(100vh - 280px);

    flex-grow: 1;

    margin: $padding-and-margin 0;
    padding: $padding-and-margin 0.5rem;
    background-color: $bg-color;
    overflow: hidden;

    @media (min-width: 375px) {
      max-height: calc(100vh - 260px);
    }
    @media (min-width: 475px) {
      max-height: calc(100vh - 240px);
    }
    @media (min-width: 567px) {
      max-height: 600px;
    }

    @include for-size(tablet-landscape-up) {
      max-height: 500px;
      padding: $padding-and-margin;
    }

    & select {
      max-width: 100px;
    }

    &-row {
      align-items: center;
    }
    &-footer {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      width: 100%;

      @include for-size(tablet-portrait-down) {
        z-index: 101;
        position: fixed;
        bottom: 0%;
        left: 0;
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;
        align-items: normal;

        width: 100%;
        padding: 10px;

        font-size: small;

        background-color: #f7f7f8;
        border-top: 1px solid $border-base;

        & .d-flex.d-sm-none {
          align-items: flex-end;
          flex-grow: 1;
          flex-wrap: nowrap;

          & .d-flex.d-sm-none span {
            margin-left: 5px;
          }
        }

        & .button,
        & .link {
          padding: 0.5rem 1rem;
          margin: 0 5px;
          line-height: 24px;
        }
      }
    }

    &-footer-mob {
      display: none;

      @include for-size(tablet-portrait-down) {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;

        width: 100%;
        padding: 10px;

        font-size: 80%;

        background-color: #f7f7f8;
        border-top: 1px solid $border-base;

        & div {
          display: flex;
          align-items: flex-end;
          flex-grow: 1;

          & span {
            margin-left: 5px;
          }
        }

        & .button {
          font-size: 80%;
          padding: 0.5rem 1rem;
          margin: 0 5px;
        }
      }
    }

    & .scroll-content {
      padding: $padding-and-margin;
      margin-bottom: $padding-and-margin;
      width: 100%;
      border: 1px solid $border-base;
      border-radius: 5px;
      background-color: #ffffff;
    }
  }
  &__content-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;

    &:not(:first-child) {
      display: none;
    }
  }
}

